body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.selected {
    z-index: 2;
    border: 2px solid blue !important;
}

.player1 {
  z-index: 2;
  border: 2px solid blue !important;
}

.player2 {
  z-index: 2;
  border: 2px solid rgb(3, 187, 3) !important;
}

.card {
  width: 120px;
  height: 165px;
  margin: 5px;
  border: 1px solid black;
  border-radius: 10%;
  display: inline-flex;
  flex-direction: column;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  background-color: white;
  text-align: center;
}

.blank {
  border: 1px solid black;
  margin: 5px;
  width: 130px;
  height: 190px;
  display: inline-block;
}

  .header {
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    /* width: 94%;
    height: 20px; */
    margin-top: 10px;
    padding-top: 3px;
    display: block;
    padding-bottom: 2px;
    padding-left: 5px;
    padding-right: 3px;
    font-family: sans-serif;
    font-size: 15px;
    font-weight: 800;
    margin-bottom: 4px;
  }
  .description {
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
  }

  .stat-item-top {
    /* grid-column-start: 1;
    grid-column-end: 3; */
    display: block;
  }

  .stat-item-middle {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: space-evenly;
  }

  .footer {
    border-top: 1px solid black;
    /* border-bottom: 1px solid black; */
    width: 94%;
    height: 18px;
    padding-bottom: 2px;
    padding-left: 5px;
    padding-right: 3px;
    font-family: sans-serif;
    font-size: 15px;
    font-weight: 500;
  }


.hand {
    display: inline-grid;
    grid-template-rows: 8.5em 8.5em 8.5em 8.5em 8.5em;
    height: 46em;
    text-align: center;
    width: 8.5em;
    border: 2px solid black;
}

.board {
    /* margin: 5px; */
    width: 100%;
    height: 100%;
    max-height: 100%;
    /* display: grid;
    grid-template-columns: auto auto auto; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
}

.first-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.board-hand {
    width: auto;
    height: 100%;
    text-align: center;
    padding: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.player-title {
    margin: 2px;
}

.turn-info {
    margin-top: 10px;
    margin-bottom: 20px;
}


.grid {
    width: 32em;
    height: auto;
    margin: auto;
    border: 2px solid black;
    /* display: inline-grid;
    grid-template-columns: 3;
    grid-template-rows: 3; */
}

.row {
    padding-left: auto;
    padding-right: auto;
    height: 200px;
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: space-between;
}

.blank {
    border: 1px solid black;
  margin: 5px;
  width: 130px;
  height: 180px;
  display: inline-block;
}

.blank-selected {
    border: 2px solid blue;
  margin: 5px;
  width: 130px;
  height: 190px;
  display: inline-block;
}
